import React, { useEffect } from 'react';

declare global {
    interface Window {
        googleTranslateElementInit?: () => void;
        google?: any;
    }
}

const GoogleTranslate: React.FC = () => {
    useEffect(() => {
        let script: HTMLScriptElement | null = null;

        const addGoogleTranslateScript = () => {
            // Remove existing script if any
            const existingScript = document.querySelector('script[src*="translate.google.com"]');
            if (existingScript) {
                existingScript.remove();
            }

            script = document.createElement('script');
            script.type = 'text/javascript';
            script.src = '//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit';
            script.async = true;
            document.body.appendChild(script);
        };

        window.googleTranslateElementInit = () => {
            // Clear existing content
            const element = document.querySelector('#google_translate_element');
            if (element) {
                element.innerHTML = '';
            }
            
            new window.google.translate.TranslateElement(
                { pageLanguage: 'en' },
                'google_translate_element'
            );
        };

        addGoogleTranslateScript();

        // Cleanup function
        return () => {
            if (script && document.body.contains(script)) {
                document.body.removeChild(script);
            }
        };

    }, []);

    return (
        <>
        <div id="google_translate_element" style={{ zIndex: 1000, position: 'absolute', top: 20, right: 150, backgroundColor:'white', padding:'10px', borderRadius:'20px'}}></div>
        </>
    );
};

export default GoogleTranslate;